.layout {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 67%;
  margin: auto;

  .line {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -10;
    opacity: 0.2;
    transform: rotate(180deg);
  }

  h1 {
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 58px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  p {
    color: #eaeaea;
    text-align: center;
    font-family: "Rubik";
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .socials {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    a {
      text-decoration: none;
      cursor: pointer;
      width: 100%;
}

    .vk {
      color: gray;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 400;
      line-height: 200%; /* 90px */
      transition: color 0.2s ease-in-out;
    }

    .vk:hover {
      color:#4c75a3;
    }

    .discord {
      color: gray;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 400;
      line-height: 200%; /* 90px */
      transition: color 0.2s ease-in-out;
    }

    .discord:hover {
      color: #7289da;
    }

    .steam {
      color: gray;
      text-align: center;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 400;
      line-height: 200%; /* 90px */
      transition: color 0.2s ease-in-out;
    }

    .steam:hover {
      color:#156da1;
    }

    .tg {
      color: gray;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 400;
      line-height: 200%; /* 90px */
      transition: color 0.2s ease-in-out;
    }

    .tg:hover {
      color: #29aaeb;
    }

    .youtube {
      color: gray;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 400;
      line-height: 200%; /* 90px */
      transition: color 0.2s ease-in-out;
    }

    .youtube:hover {
      color: #f00;
    }
  }
}
@media (max-width: 1580px) {
  .contacts {
    width: 90%;
    p {
      font-weight: 300;
    }
    .socials {
      flex-wrap: wrap;
      justify-content: center;
      p {
        margin: 0 20px;
      }
      a {
        width: 28%;
      }
    }
    .line {
      height: 250px;
    }
  }
}
@media (max-width: 900px) {
  .contacts {
    width: 90%;
    h1 {
      font-size: 40px;
    }
    p {
      font-size: 21px;
    }
    .socials {
      a {
        font-size: 40px !important;
        width: 32%;
      }

      .discord {
        padding-left: 3%;
      }
    }
  }
}
@media (max-width: 720px) {
  .contacts {
    .line {
      height: 120px;
    }
    h1 {
      font-size: 15px;
    }
    p {
      font-size: 11px;
    }
    .socials {
      a {
        font-size: 15px !important;
      }

      .vk {
        font-size: 15px !important;
      }
    }
  }
}
