body.modal-open {
  overflow: hidden;
}

.noBlack {
  display: none;
}

.black {
  position: fixed;
  min-width: 100%;
  // height: 100%;
  min-height: 100vh;
  opacity: 0.5;
  background-color: #000;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.all {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.vacation {
  display: flex;
  flex-direction: row !important;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-radius: 10px;
  background: rgba(214, 185, 252, 0.5);
  padding: 5%;
  margin-top: 5%;
  position: relative;

  .left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    h5 {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin: 0%;
    }

    p {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }
    .hidden{
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      height: 0px;
      transition: height 0.2s ease-in-out;
      overflow: hidden;
      -webkit-transition: height 0.2s ease-in-out;
      -moz-transition: height 0.2s ease-in-out;
      -ms-transition: height 0.2s ease-in-out;
      -o-transition: height 0.2s ease-in-out;
}
    .extra {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      height: 100%;
      transition: height 0.2s ease-in-out;
      overflow: hidden;
      .more
{
        width: 100%;
        opacity: 0.8;
        cursor: pointer;
      }

      .more:hover {
        opacity: 1;
      }
    }
    .up{
      rotate: 180deg;
      margin-left: 20px;
      cursor: pointer;
      transition: rotate 0.2s ease-in-out;
    }

    .down {
      margin-left: 20px;
      cursor: pointer;
      transition: rotate 0.2s ease-in-out;
      -webkit-transition: rotate 0.2s ease-in-out;
      -moz-transition: rotate 0.2s ease-in-out;
      -ms-transition: rotate 0.2s ease-in-out;
      -o-transition: rotate 0.2s ease-in-out;
}

    .more p,
    .more span {
      color: rgba(234, 234, 234, 1);
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      margin: 0%;
      cursor: pointer;
      height: 100%;
}

    .more span:hover {
      text-decoration: underline;
    }

    .extra span:hover {
      text-decoration: underline;
      color: rgba(234, 234, 234, 1);
      cursor: pointer;
    }

    .extra img {
      // margin: 0%;
      padding-left: 3%;
    }

    .noDescription {
      height: 0px;
      transition: height 0.2s ease-in-out;
      -webkit-transition: height 0.2s ease-in-out;
      -moz-transition: height 0.2s ease-in-out;
      -ms-transition: height 0.2s ease-in-out;
      -o-transition: height 0.2s ease-in-out;
}

    .description {
      display: flex;
      flex-direction: column;
      align-items: flex-start !important;
      width: 100%;
      height: 100%;
    }

    .description p {
      margin-top: 5%;
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    .info {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 15%;
      margin-top: 3%;
      opacity: 0.8;
      cursor: pointer;

      .arrow2 {
        transform: rotate(180deg);
        margin-left: 10%;
        -webkit-transform: rotate(180deg);
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -o-transform: rotate(180deg);
      }
      .arrow2:hover {
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
}
    }

    .info:hover {
      opacity: 1;
    }
  }

  button {
    border: 4px solid #ffaa50;
    background: none;
    padding: 1%;
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 29px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
}

  button:hover {
    border-color: rgba(255, 170, 80, 0.1);
    background: rgba(255, 170, 80, 0.5);
  }
}

.inActive {
  width: 0;
  height: 0;
  opacity: 0;
}

.popup {
  position: fixed;
  top: 50px;
  bottom: 70px;
  z-index: 200;
  overflow-y: scroll;
  border-radius: 10px;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;

  .nowrapper {
    opacity: 0;
  }
  

  .wrapper {
    background-color: #eaeaea;
    border-radius: 10px;
    z-index: 10;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;
    // margin-bottom: 3%;

    .top {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      // width: 100%;
      background-color: #ffaa50;
      padding: 40px;

      h1 {
        margin: 0%;
        color: #eaeaea;
        font-family: "Rubik";
        font-size: 40px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
      }

      img {
        max-width: 20px;
        max-height: 20px;
        cursor: pointer;
      }
    }

    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 30px;
      // padding: 2%;
      // width: 100%;

      p {
        color: #101010;
        font-family: "Rubik";
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
      }
    }

    form {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      .element {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        // padding-left: 90px;
      }

      label {
        color: #101010;
        font-family: "Rubik";
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        margin: 1%;
        cursor: pointer;
      }

      input,
      textarea {
        border-radius: 10px;
        border: 5px solid #ffaa50;
        width: 100%;
        color: rgba(16, 16, 16, 0.5);
        font-family: "Rubik";
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        background: none;
        margin: 0%;
        box-sizing: border-box;
        padding: 14px;
        margin-bottom: 20px;
      }

      textarea {
        resize: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        // padding: 11px 36px;
      }

      textarea:focus {
        outline: none;
      }

      textarea::-webkit-input-placeholder {
        // padding: 11px 0;
        margin: auto;
        height: auto;
      }

      button {
        background: #ffaa50;
        color: #eaeaea;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        padding: 5%;
        margin-top: 5%;
        border: none;
}

      button:hover {
        background-color: rgba(255, 131, 0, 1);
      }

      .bottom {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
        margin-top: 5%;
        margin-bottom: 5%;

        label {
          display: flex;
          flex-direction: row;
          align-items: center;
          width: 100%;
        }

        .tick {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          border-radius: 5px;
          border: 1px solid #101010;
          width: 15px !important;
          height: 15px !important;
          padding: 6px;
          cursor: pointer;
        }

        .tick img {
          opacity: 0;
          transition: opacity 0.2s ease-in-out;
        }

        label p {
          margin-left: 10px;
          width: 100%;
          font-size: 18px;
          text-decoration: none;
          color: #101010;
          font-family: "Rubik";
          font-style: normal;
          font-weight: 300;
          line-height: normal;
        }

        label a {
          text-decoration: none;
          color: #101010;
          text-decoration: underline;
          color: #ffaa50;
        }

        label a:hover {
          text-decoration: underline;
          color: #ffaa50;
        }

        input:checked ~ .tick img {
          opacity: 1;
        }

        input {
          display: none;
        }
      }
    }
  }
}
@media (max-width: 1024px) {
  .vacation {
    .left {
      .description {
        div {
          p {
            font-size: 23px;
          }
        }
      }
      margin-right: 40px;
      h5 {
        font-size: 23px;
      }
      p {
        font-size: 23px;
      }
      .extra {
        span {
          font-size: 23px;
        }
      }
    }
    button {
      font-size: 23px;
      box-sizing: border-box;
      padding: 28px;
    }
    .popup {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 50%;
      h1 {
        font-size: 23px;
        padding: 5% 5%;
      }
      img {
        width: 20px;
        top: 2.5%;
      }
      .form {
        padding: 0;
        p {
          font-size: 18px;
        }
      }
      form {
        .element {
          width: 90%;
          padding-left: 0 !important;
        }
        .bottom {
          width: 90%;
          label {
            margin-left: 10px;
            font-size: 18px;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .vacation {
    .left {
      margin-left: 16px;
      .description {
        div {
          p {
            font-size: 21px;
          }
        }
      }
      p {
        font-size: 21px;
      }
      .extra {
        span {
          font-size: 21px;
        }
      }
    }
    button {
      font-size: 21px;
    }
    .popup {
      width: 70%;
      h1 {
        font-size: 18px;
        padding: 5% 5%;
      }
      img {
        width: 20px;
        top: 2.5%;
      }
      .form {
        padding: 0;
        p {
          width: 90%;
        }
        input,
        textarea {
          height: 35px;
          width: 80%;
          font-size: 12px;
          padding-left: 10px;
        }
        label {
          font-size: 15px;
          margin-top: 15px;
        }
        p {
          font-size: 15px;
        }
        button {
          padding: 2%;
          font-size: 20px;
        }
      }
      form {
        .bottom {
          input,
          textarea {
            width: 20px;
          }
          label {
            margin-left: 10px;
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media (max-width: 560px) {
  .vacation {
    flex-direction: column !important;
    align-items: center;
    justify-content: center;
    padding: 10%;
    box-sizing: border-box;
    .left {
      align-items: center;
      text-align: center;
      .description {
        div {
          p {
            font-size: 13px;
          }
        }
      }
      justify-content: center;
      align-items: center;
      margin: 0;
      width: 100%;
      h5 {
        font-size: 13px;
        text-align: center;
      }
      p {
        font-size: 11px;
      }
      .extra {
        justify-content: center;
        span {
          font-size: 11px;
        }
        img {
          width: 15px;
        }
      }
    }
    button {
      margin-top: 20px;
      font-size: 15px;
      width: 100%;
      padding: 15px;
    }
  }
  .popup {
    .wrapper {
      .top {
        h1 {
          font-size: 24px !important;
        }
      }
    }
    .form {
      input,
      textarea {
        font-size: 11px !important;
      }
      label {
        font-size: 11px !important;
        margin-top: 15px !important;
      }
      p {
        font-size: 11px !important;
      }
      button {
        padding: 3% !important;
        font-size: 15px !important;
        width: 100% !important;
      }
    }
    form {
      .bottom {
        label {
          margin-left: 10px !important;
          margin-top: 0px !important;
          font-size: 10px !important;
        }
        .tick {
          width: 30px !important;
          height: 30px !important;
          padding: 10px !important;
          box-sizing: border-box !important;

          img {
            width: 10px !important;
            height: 10px !important;
          }
        }
      }
    }
  }
}
