.header {
  background-color: rgba(16, 16, 16, 0.7);
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;

  .nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 67%;
  }

  .link {
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 1.25vw;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration: none;
    text-transform: uppercase;
    transition: opacity 0.2s ease-in-out;
    -webkit-transition: opacity 0.2s ease-in-out;
    -moz-transition: opacity 0.2s ease-in-out;
    -ms-transition: opacity 0.2s ease-in-out;
    -o-transition: opacity 0.2s ease-in-out;
}

  .link:hover {
    opacity: 0.5;
  }

  .icon img {
    margin-bottom: 0%;
  }
  .burgerMenuButton {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
    display: none;
    margin-right: 45px;
  }
  .burgerMenuWrapper {
    display: none;
  }
  .burgerMenu {
    display: none;
    nav {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      .top {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        p {
          color: rgba(255, 255, 255, 0.6);
          font-family: "Rubik";
          font-size: 16px;
          font-style: normal;
          font-weight: 300;
          line-height: normal;
          text-transform: uppercase;
          margin: 0%;
        }
      }
      .middle {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        margin-top: 35px;
        p {
          color: rgba(255, 255, 255, 0.6);
          font-family: "Rubik";
          font-size: 16px;
          font-style: normal;
          font-weight: 300;
          line-height: normal;
          text-transform: uppercase;
          margin: 0%;
        }
      }
      .bottom {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        margin-top: 277px;
      }
    }
  }
}
@media (max-width: 1024px) {
  .header {
    justify-content: space-between;
    .nav {
      display: none;
    }
    .burgerMenuButton {
      display: block;
      position: relative;
      z-index: 100;
    }
    .burgerMenuWrapper {
      top: 0;
      left: 0;
      position: fixed;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      display: block;
      z-index: 50;
      overflow: hidden;
    }
    .burgerMenu {
      position: absolute;
      z-index: 100;
      right: 130px !important;
      top: 70px;
      right: 130px;
      display: flex;
      padding: 50px;
      border: 2px solid #fff;
      background-color: rgba(16, 16, 16, 1);
      nav {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        max-width: 366px;
        padding-right: 12px;
        .link {
          margin: 12px 0;
          width: 100%;
          font-size: 24px;
          .btn {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-radius: 10px;
            border: none;
            color: #fff !important;
            font-family: "Rubik";
            font-size: 24px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
            text-transform: none !important;
            box-sizing: border-box;
            height: 58px;
            background: none;

            div {
              width: 100%;
              height: 100%;
            }

            span {
              margin-left: 13px;
            }

            img {
              width: 26px !important;
              height: 26px !important;
              margin-left: 4px;
            }
          }

          .btnActive {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-radius: 10px;
            border: none;
            color: #fff !important;
            font-family: "Rubik";
            font-size: 24px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
            text-transform: none !important;
            box-sizing: border-box;
            height: 58px;
            background: none;
            // border: 2px solid rgba(255, 255, 255, 1);

            div {
              width: 100%;
              height: 100%;
              // border: none;
              background: rgba(255, 170, 80, 1);
            }

            span {
              margin-left: 13px;
            }

            img {
              margin-left: 4px;
            }
          }
          button {
            width: 100%;
          }
        }
      }
    }
  }
}
@media (max-width: 450px) {
  .header {
    img {
      width: 50%;
    }
    .burgerMenuButton {
      margin-right: 10px;
    }
    .burgerMenu {
      right: 10px !important;
      padding: 20px 22px;
      nav {
        padding-right: 30px;
        .bottom {
          margin-top: 72px;
        }
      }
    }
  }
}
