.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  .top {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 67%;

    h1 {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 58px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    p {
      color: #fff;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }
  }

  .main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 67%;

    .block {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between;
      width: 100%;
      margin-top: 134px;

      h3 {
        color: #eaeaea;
        font-family: "Rubik";
        font-size: 58px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin: 0%;
      }

      p {
        color: #fff;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        width: 425px;
      }
    }
  }

  .bottom {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 67%;
    margin: auto;
    padding-top: 3%;
    position: relative;

    .column {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
    }

    .column h3 {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 64px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin-bottom: 0%;
      text-align: center !important;
    }

    .column p {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-weight: 300;
      line-height: normal;
    }
  }

  .line {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -10;
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
  }

  button {
    position: absolute;
    bottom: -70px;
    right: 0px;
  }
}
@media (max-width: 1750px) {
  .project {
    overflow: hidden;
    .main,
    .top,
    .bottom {
      width: 80%;
    }
  }
}

@media (max-width: 1500px) {
  .project {
    overflow: hidden;
    width: 90%;
    margin: auto;
    .line {
      height: 250px;
    }
    .top {
      width: 90%;
      text-align: center;
    }
    .main {
      width: 90%;
      .block {
        img {
          width: 60%;
        }
        div {
          width: 35%;
          h3 {
            font-size: 45px;
          }
          p {
            font-size: 20px;
            width: auto;
          }
        }
      }
    }
    .bottom {
      width: 90%;
      .column {
        width: 100%;
        max-width: 33%;
        text-align: center;
        img {
          height: 119px;
        }
        p {
          height: 50px;
          max-height: 40px;
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .project {
    padding-bottom: 96px;
    .line {
      height: 200px;
    }
    .top {
      width: 90%;
      text-align: center;
      p {
        font-size: 21px;
      }
    }
    .main {
      width: 90%;
      .block {
        flex-direction: column;
        align-items: center;
        text-align: center;
        img {
          width: 90%;
        }
        div {
          width: 90%;
          h3 {
            font-size: 40px;
            margin-top: 50px;
          }
          p {
            font-size: 20px;
            width: auto;
          }
        }
      }
      .block:nth-child(2) {
        div {
          order: 2;
        }
        img {
          order: 1;
        }
      }
    }
    .main {
      .bottom {
        margin-top: 100px;
        width: 90%;
        .scrollBtn button {
          display: none !important;
        }
        .column {
          width: 100%;
          max-width: 33%;
          text-align: center;
          img {
            height: 119px;
          }
          p {
            height: 50px;
            max-height: 40px;
          }
        }
      }
    }
  }
  .Project_project__qy3XE button {
    display: none !important;
  }
}
@media (max-width: 450px) {
  .project {
    .line {
      height: 120px;
    }
    .top {
      h1 {
        font-size: 17px;
        margin-top: 50px;
      }
      p {
        font-size: 11px;
      }
    }
    .main {
      .block {
        margin-top: 50px;
        img {
          width: 90%;
        }
        div {
          width: 90%;
          h3 {
            font-size: 17px;
            margin-top: 20px;
          }
          p {
            font-size: 11px;
          }
        }
      }
    }
    .bottom {
      margin-top: 50px;
      .column {
        img {
          height: 35px;
        }
        h3 {
          font-size: 15px;
        }
        p {
          font-size: 11px;
        }
      }
    }
  }
}
