.person {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  // max-width: 358px;
  width: 26%;

  .blur {
    filter: blur(2px);
    -webkit-filter: blur(2px);
  }
  div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  img {
    margin-bottom: 5%;
    transition: filter 0.2s ease-in-out;
    -webkit-transition: filter 0.2s ease-in-out;
    -moz-transition: filter 0.2s ease-in-out;
    -ms-transition: filter 0.2s ease-in-out;
    -o-transition: filter 0.2s ease-in-out;
}

  h5 {
    color: #fff;
    font-family: "Rubik";
    font-size: 29px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin: 0%;
    text-align: center;
  }

  p {
    color: #fff;
    font-family: "Rubik";
    font-size: 29px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    text-align: center;
  }
}
@media (max-width: 1750px) {
  .person {
    margin: 50px;
    width: 40%;
  }
}
@media (max-width: 1024px) {
  .person {
    width: 50%;
  }
}
@media (max-width: 580px) {
  .person {
    img {
      width: 290px;
    }
    h5 {
      font-size: 15px;
    }
    p {
      font-size: 11px;
    }
  }
}
