.event {
  display: flex;
  flex-direction: row !important;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-radius: 10px;
  background: rgba(214, 185, 252, 0.5);
  padding: 5%;
  margin-top: 5%;

  .left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;

    h5 {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin: 0%;
    }

    p {
      color: #eaeaea;
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    .extra {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;

      .more {
        width: 100%;
        opacity: 0.8;
        cursor: pointer;
      }

      .more:hover {
        opacity: 1;
      }
    }

    .extra p,
    .extra span {
      color: rgba(234, 234, 234, 1);
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
      margin: 0%;
    }

    .extra span:hover {
      text-decoration: underline;
      cursor: pointer;
    }

    .extra img {
      margin: 0%;
      padding-left: 3%;
    }

    .description {
      display: flex;
      flex-direction: column;
      align-items: flex-start !important;
      width: 100%;

      .image {
        padding: 0%;
        margin-top: 120px;
      }

      img {
        width: 100%;
      }
    }

    .description p {
      margin-top: 5%;
      color: rgba(234, 234, 234, 1);
      font-family: "Rubik";
      font-size: 29px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    .description img {
      margin: auto;
      padding-top: 3%;
    }

    .info {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 15%;
      margin-top: 3%;
      opacity: 0.8;
      cursor: pointer;

      img {
        max-width: 25px;
        padding-top: 0%;
      }
    }

    .info:hover {
      opacity: 1;
    }
  }
}
@media (max-width: 1024px) {
  .event {
    .left {
      .extra {
        .description {
          .image {
            margin-top: 32px;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .event {
    .left {
      h5 {
        font-size: 23px;
      }
      p {
        font-size: 21px;
      }
      .extra {
        .description {
          p {
            font-size: 21px;
          }
        }
        span {
          font-size: 21px;
        }
      }
    }
  }
}

@media (max-width: 450px) {
  .event {
    .left {
      h5 {
        font-size: 13px;
      }
      p {
        font-size: 11px;
      }
      .extra {
        span {
          font-size: 11px;
        }
        img {
          width: 15px;
        }
      }
      .description {
        width: 100%;

        img {
          width: 100% !important;
        }
      }

      .description p {
        font-size: 11px;
        width: 100%;
      }
    }
  }
}

@media (max-width: 450px) {
  .event {
    .left {
      align-items: center;
      text-align: center;
      h5 {
        font-size: 13px;
      }
      p {
        font-size: 11px;
      }
      .extra {
        span {
          font-size: 11px;
        }
        img {
          width: 15px;
        }
        .description {
          .image {
            margin-top: 5%;
          }
          .info {
            width: 30%;
            margin: auto;
            margin-top: 3%;

            img {
              width: 15px !important;
            }
          }
          div {
            width: 100%;
          }
          p {
            font-size: 11px;
            a {
              font-size: 9px;
            }
          }
        }
      }
    }
  }
}
