.documents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 67%;
  margin: auto;

  .line {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -10;
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
  }

  h1 {
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 58px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .tabs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 90%;

    .inActive {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      padding: 1% 2% 1% 2%;
      transition: background-color 0.2s ease-in-out;
      -webkit-transition: background-color 0.2s ease-in-out;
      -moz-transition: background-color 0.2s ease-in-out;
      -ms-transition: background-color 0.2s ease-in-out;
      -o-transition: background-color 0.2s ease-in-out;
}

    .inActive:hover {
      border-radius: 10px;
      background-color: rgba(214, 185, 252, 0.5);
      padding: 1% 2% 1% 2%;
    }

    .active {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      border-radius: 10px;
      background: rgba(255, 170, 80, 0.5);
      padding: 1% 2% 1% 2%;
    }

    .active2 {
      color: #eaeaea;
      text-align: center;
      font-family: "Rubik";
      font-size: 45px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      cursor: pointer;
      border-radius: 10px;
      background: rgba(214, 185, 252, 0.5);
      padding: 1% 2% 1% 2%;
    }
  }

  .main {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;

    .column {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      width: 30%;

      button {
        color: #fff;
        border: none;
        text-align: center;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        border-radius: 10px;
        background: rgba(255, 170, 80, 0.5);
        width: 327px;
        height: 115px;
        -webkit-border-radius: 10px;
        -moz-border-radius: 10px;
        -ms-border-radius: 10px;
        -o-border-radius: 10px;
        margin-bottom: 35px;
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        transition: transform 0.2s ease-in-out;
        -webkit-transition: transform 0.2s ease-in-out;
        -moz-transition: transform 0.2s ease-in-out;
        -ms-transition: transform 0.2s ease-in-out;
        -o-transition: transform 0.2s ease-in-out;
}

      button:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
}

      p {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        text-align: center;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        border-radius: 10px;
        background: rgba(255, 170, 80, 0.5) !important;
        width: 327px;
        height: 86px;
        padding: 0%;
        margin: 22px 0;
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        transition: transform 0.2s ease-in-out;
        -webkit-transition: transform 0.2s ease-in-out;
        -moz-transition: transform 0.2s ease-in-out;
        -ms-transition: transform 0.2s ease-in-out;
        -o-transition: transform 0.2s ease-in-out;
      }

      p:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
}
    }

    .column2 {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      button {
        color: #fff;
        text-align: center;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        border-radius: 10px;
        background: rgba(214, 185, 252, 0.5);
        width: 327px;
        height: 115px;
        margin-bottom: 35px;
        border: none;
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        transition: transform 0.2s ease-in-out;
        -webkit-transition: transform 0.2s ease-in-out;
        -moz-transition: transform 0.2s ease-in-out;
        -ms-transition: transform 0.2s ease-in-out;
        -o-transition: transform 0.2s ease-in-out;
      }

      button:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
}

      p {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        text-align: center;
        font-family: "Rubik";
        font-size: 29px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        border-radius: 10px;
        background: rgba(214, 185, 252, 0.5);
        width: 327px;
        height: 86px;
        margin: 22px 0;
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
        -moz-transform: scale(1.0);
        -ms-transform: scale(1.0);
        -o-transform: scale(1.0);
        transition: transform 0.2s ease-in-out;
        -webkit-transition: transform 0.2s ease-in-out;
        -moz-transition: transform 0.2s ease-in-out;
        -ms-transition: transform 0.2s ease-in-out;
        -o-transition: transform 0.2s ease-in-out;
      }
      p:hover {
        transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -ms-transform: scale(1.1);
        -o-transform: scale(1.1);
}
    }
  }
}
