                  width: 100%;
                  height: 100%;
                }

                span {
                  margin-left: 13px;
                }

                img {
                  width: 26px !important;
                  height: 26px !important;
                  margin-left: 4px;
                }
              }
              .btnActive {
                display: flex;
                flex-direction: row;
                align-items: center;
                border-radius: 10px;
                border: none;
                color: #fff !important;
                font-family: "Rubik";
                font-size: 24px;
                font-style: normal;
                font-weight: 300;
                line-height: normal;
                text-transform: none !important;
                box-sizing: border-box;
                height: 58px;
                background: none;
