                div {
                  width: 100%;
                  height: 100%;
                  background: rgba(255, 170, 80, 1);
                }

                span {
                  margin-left: 13px;
                }

                img {
                  width: 26px !important;
                  height: 26px !important;
                  margin-left: 4px;
                }
              }
            }
            .bottom {
              display: flex !important;
            }
          }
        }
        .burger {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          background: none;
          border: none;
          cursor: pointer;
          img {
            width: 100% !important;
          }
        }
        .divider,
        h2,
        p {
          display: none;
        }
        .menu {
          display: none !important;
        }
      }
      .bottom {
        display: none !important;
      }
    }

    .right {
      width: 95%;
      margin: auto;

      .calendar {
        width: 98% !important;
      }

      .text {
        h3 {
          font-size: 30px;
        }

        p {
          font-size: 15px !important;
        }
      }

      .main {
        .info {
          .element {
            .discord {
              h3 {
                font-size: 15px !important;
              }

              .account {
                .username {
                  p {
                    font-size: 12px !important;
                  }

                  span {
                    font-size: 12px !important;
                  }
                }
              }

              .change {
                flex-direction: row;
                width: 82%;

                button {
                  font-size: 15px !important;
                  margin-right: 5%;
                }
              }
            }
          }

          .data {
            h4 {
              font-size: 15px;
            }

            input {
              font-size: 15px;
            }
          }
        }
      }
    }
  }
}

