.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: none;
  color: #fff !important;
  font-family: "Rubik";
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  padding-right: 2%;
  margin-top: 3%;
  text-transform: none !important;
  // height: 60px;
  box-sizing: border-box;
  padding: 16px 0;

  span {
    margin-left: 13px;
  }

  img {
    margin-left: 4px;
  }
}

