//   background: rgba(255, 170, 80, 0.5);
// }

.btn:hover {
  border-radius: 10px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: rgba(255, 170, 80, 0.5);
}

@media (max-width: 768px) {
  .btn {
    width: 100%;
    padding: 10px 20px;
    margin-left: -10px;

    img {
      width: 10%;
    }
  }
}
@media (max-width: 450px) {
  .btn {
    width: 100%;
    padding: 5px 10px;
    img {
      width: 15px;
    }
    p {
      margin-left: 2%;
    }
  }
}
