.team {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 67%;
  margin: auto;

  h1 {
    color: #eaeaea;
    font-family: "Rubik";
    font-size: 58px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  .main {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  .line {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -10;
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
  }
}
@media (max-width: 1500px) {
  .team {
    width: 90%;
    .line {
      height: 250px;
    }
    h1 {
      font-size: 48px;
    }
    .main {
      justify-content: center;
      width: 100%;
    }
  }
}

@media (max-width: 768px) {
  .team {
    padding-bottom: 195px;
  }
}

@media (max-width: 450px) {
  .team {
    h1 {
      font-size: 17px;
    }
    .line {
      height: 120px;
    }
  }
}
